.input-container {
    border: 1px solid rgb(201, 201, 201);
    border-radius: 0.5rem;
    padding: 0.5rem;
    justify-content: space-between;
    width: 50%;
    position: relative;
    margin-bottom: 2rem;
}
.input-container label{
    position: absolute;
    top: -1.5rem;
    left: 0;
}
.select-container {
    border: 1px solid rgb(201, 201, 201);
    border-radius: 0.5rem;
    padding: 0.5rem;
    justify-content: space-between;
    width: 33%;
    position: relative;
}
.checkbox-container {
    justify-content: flex-start;
    width: 100%;
    position: relative;
    margin-bottom: 2rem;
}
.checkbox-container label{
    display: flex;
    width: auto;
    align-items: center;
}
.checkbox-container input{
    width: 16%;
    margin-right: 0.2rem;
}

input, select{
    border: none;
    width: 100%;
}   

.error-message {
    color: red;
    position: absolute;
    margin-top: 2.5rem;
    width: 100%;
}

@media screen and (max-width: 47.938em) {
    .input-container {
        width: 100%;
    }
    .select-container {
        width: 100%;
    }
}