.top {
    color: black;
    font-size: 30px;

}

.sidebar {
    background-color: #fff;
    border-radius: 0 0 5px 0;
    flex: 1;
    border-right: .5px solid #EC8325;
    min-height: 100vh;
}

.bar {
    background-color: #000;
    border-radius: 0 0 5px 0;
    flex: 1;
    border-right: .5px solid #EC8325;
    min-height: 100vh;
}

.top {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.top .titleSide {
    font-size: 20px;
    font-weight: bold;
    color: #EC8325;
}

.sidebar hr {
    height: 0px;
    margin-top: -3px;
    border: .5px solid #EC8325;
}

.sidebar .center {
    padding-left: 10px;
}

.sidebar .center ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.centerP {
    font-size: 17px;
    font-weight: bold;
    color: #999;
    margin-top: 15px;
    margin-bottom: 5px;
}

.center ul li {
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
    padding-right: 20px;
    font-size: large;
}

.center ul li svg {
    color: #EC8325;
    font-size: 16px;
    margin-right: 10px;
}

.center ul li:hover {
    background-color: #fff3e8;
}