.modalcontainer {
    width: 50vw;
}
.modalscroll {
    width: 47vw;
}

@media screen and (max-width: 37.5em) {
    .modalcontainer {
        width: 80vw;
    }
    .modalscroll {
        width: 75vw;
    }
}
@media screen and (min-width: 37.5em) and (max-width: 64em) {
    .modalcontainer {
        width: 80vw;
    }
    .modalscroll {
        width: 75vw;
    }
}
.modalcontainer h3 {
    margin-top: -4rem;
    margin-bottom: 2rem;
}
.card {
    transition: all .2s;
    cursor: pointer;
    text-align: center;
}
.card-header {
    transition: all .2s;
}
.card:hover {
    border: 1px solid orange;
}
.card:hover > .card-header {
    background-color: rgb(255, 181, 45);
}